<template>
  <div>
    <h2>Animated Buttons</h2>
    <div>
      <button class="qr-button bounce-button">Bounce</button>
      <button class="qr-button slide-button">Slide</button>
      <button class="qr-button rotate-button">Rotate</button>
      <button class="qr-button grow-button">Grow</button>
      <button class="qr-button shrink-button">Shrink</button>
      <button class="qr-button pulse-button">Pulse</button>
      <button class="qr-button swing-button">Swing</button>
      <button class="qr-button wave-button">Wave</button>
      <button class="qr-button flash-button">Flash</button>
      <button class="qr-button rubberband-button">Rubber Band</button>
    </div>
  </div>
</template>

<style>
/* Common button styles */
/* .qr-button {
  padding: 15px 30px;
  font-size: 16px;
  color: #fff;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-block;
  margin: 10px;
  background: linear-gradient(45deg, #ff00cc, #3333ff);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
} */

.qr-button {
  padding: 15px 30px;
  font-size: 16px;
  color: #fff; /* White text for good contrast */
  border: none;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-block;
  margin: 10px;
  background: #1e88e5;

  /* Option 1: Blue Gradient (Classic and Trustworthy) */
  background: linear-gradient(45deg, #3546ff, #00a3b8);

  /* Option 2: Orange Gradient (Energetic and Vibrant) */
  /* background: linear-gradient(45deg, #ff9800, #f44336); */

  /* Option 3: Purple Gradient (Modern and Sophisticated) */
  /* background: linear-gradient(45deg, #7e57c2, #d330e7);  Purple and Pink */

  /* Option 4: Teal Gradient (Calming and Professional) */
  /* background: linear-gradient(45deg, #20c997, #1e88e5); */

  /* Option 5: Customize further (Choose your own colors!) */
  /* background: linear-gradient(45deg, #your_color1, #your_color2); */
}

/* Bounce Button */
.bounce-button:hover {
  animation: bounce 0.5s;
}
@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

/* Slide Button */
.slide-button:hover {
  animation: slide 0.5s;
}
@keyframes slide {
  0%, 100% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(10px);
  }
}

/* Rotate Button */
.rotate-button:hover {
  animation: rotate 0.5s;
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Grow Button */
.grow-button:hover {
  animation: grow 0.5s;
}
@keyframes grow {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

/* Shrink Button */
.shrink-button:hover {
  animation: shrink 0.5s;
}
@keyframes shrink {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.9);
  }
}

/* Pulse Button */
.pulse-button:hover {
  animation: pulse 0.5s;
}
@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

/* Swing Button */
.swing-button:hover {
  animation: swing 0.5s;
}
@keyframes swing {
  0%, 100% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(15deg);
  }
}

/* Wave Button */
.wave-button:hover {
  animation: wave 0.5s;
}
@keyframes wave {
  0%, 100% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(5px);
  }
  50% {
    transform: translateX(-5px);
  }
  75% {
    transform: translateX(5px);
  }
}

/* Flash Button */
.flash-button:hover {
  animation: flash 0.5s;
}
@keyframes flash {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

/* Rubber Band Button */
.rubberband-button:hover {
  animation: rubberband 0.5s;
}
@keyframes rubberband {
  0%, 100% {
    transform: scaleX(1);
  }
  30% {
    transform: scaleX(1.25) scaleY(0.75);
  }
  40% {
    transform: scaleX(0.75) scaleY(1.25);
  }
  50% {
    transform: scaleX(1.15) scaleY(0.85);
  }
  65% {
    transform: scaleX(0.95) scaleY(1.05);
  }
  75% {
    transform: scaleX(1.05) scaleY(0.95);
  }
  100% {
    transform: scaleX(1) scaleY(1);
  }
}
</style>
